<template>
  <div>
    <!---->
    <div class="parentChildHeaderWrap">
      <div class="parentChildHeader clearfix">
        <h2 @click="goHomePage">
          <img src="../../assets/img/center.png" height="60" width="60">
          <strong></strong>
        </h2>
        <router-link to="/Comment/HotelHomePage"></router-link>
      </div>
    </div>
    <!--content-->
    <section>
      <div class="imageBox"></div>
      <!---->
      <div class="goCity">
        <strong></strong>
        <div class="goCityList">
          <a href="javascript:;" v-for="item,index in cityList" :key="index">{{item}}</a>
        </div>
      </div>
      <!---->
      <ul class="scenicSpotList">
        <li v-for="item,index in scenicSpotList">
          <h1>{{item}}</h1>
          <ul class="scenicSpotHotelList">
            <li>
              <img src="../../assets/img/homePageImage.jpg" width="312" height="235"/>
              <a href="javascript:;"></a>
              <div class="priceAndSubmit clearfix">
                <strong> <span>130</span></strong>
                <a href="javascript:;"></a>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([]),
    data() {
      return {
        cityList: [
          '',
          '',
          '',
          '',
          ''
        ],
        scenicSpotList: [
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          ''
        ],
      }
    },
    methods: {
      goHomePage() {
        this.$router.push({name: 'HotelHomePage'})
      }
    },
  }
</script>
<style scoped>
  /**/
  .parentChildHeaderWrap {
    width: 100%;
    background-color: #fff;
    padding: 20px 0;
  }

  .parentChildHeader {
    width: 1000px;
    margin: 0 auto;
  }

  .parentChildHeader > h2 {
    float: left;
  }

  .parentChildHeader > h2:hover {
    cursor: pointer;
  }

  .parentChildHeader strong {
    font: bold 30px/1 "";
    vertical-align: bottom;
    display: inline-block;
    margin-left: 10px;
    margin-bottom: 5px;
  }

  .parentChildHeader > a {
    float: right;
    font: 16px/2 "";
    padding: 0 15px;
    -webkit-border-radius: 16px;
    -moz-border-radius: 16px;
    border-radius: 16px;
    border: 1px solid #999;
    color: #999;
    margin-top: 10px;
  }

  .parentChildHeader > a:hover {
    border-color: #f60;
    color: #f60;
  }

  /**/

  /*contentSectionStart*/

  section {
    width: 100%;
  }

</style>



